a {
  width: auto;
  margin: 0;
  text-decoration: none;
  color: black;
}
.App {
  text-align: center;
}

.Nav-Bar {
  background-color: rgb(68, 68, 78);
  text-align: center;
  display: flex;
  justify-content: space-around;
  min-height: 10vh;
  width: 100%;
}

.Nav-links {
  width: 50%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
  color: #f1f1f1;
  font-size: 15px;
}
.API1 {
  display: flex;
  flex-direction: row;
}

.productImage {
  position: relative;
  left: 50%;
  margin: 5% 0;
  transform: translateX(-50%);
  width: 35%;
  min-height: 10vw;
  display: flex;
  align-items: center;
}
.productImage img {
  width: 100%;
  height: auto;
}
.productsContainer {
  display: flex;
  flex-direction: row;
  width: 100%;
  flex-wrap: wrap;
}
.productContainer {
  width: 19%;
  padding: 0 2% 2% 2%;
  margin: 1%;
  border-radius: 1.5vw;
  box-shadow: 0 0 10px #80808036;
}

.Producttitle {
  font-size: 1vw;
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box !important;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  white-space: normal;
}
.Category {
  font-style: italic;
  font-weight: 100;
  font-size: 10px;
}
.productName {
  text-decoration: none;
  font-size: 2vw;
}
.ProductDetails {
  width: 96%;
  text-align: left;
  display: flex;
  padding: 2%;
}
#Productimage {
  width: 53%;
  margin: 1%;
  display: flex;
  justify-content: center;
  align-items: center;
}
#Productimage img {
  width: 40%;
  height: auto;
}
.ProductDescp {
  font-size: 1.2vw;
  font-weight: 500;
  margin: 2% 0;
}
#Producttitle {
  width: 100%;
  font-size: 2vw;
  font-weight: bold;
  margin: 2% 0;
}
.productRight {
  width: 45%;
}
.Productprice {
  font-size: 2vw;
  font-weight: 700;
  margin: 2% 0;
}
.ratingsCount {
  padding: 0 0 0 1vw;
  font-size: calc(1vw + 5px);
}
.ratings {
  font-weight: 700;
  font-size: calc(1vw + 5px);
}
.buyBtn {
  font-size: calc(1vw + 5px);
  border: 0;
  padding: 1vw;
  margin: 2vw 2vw 0 0;
  color: white;
  background-color: gray;
  border-radius: 5px;
}
.productName {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2vw;
  padding: 0.5vw 1.25vw;
  margin: 1vw;
  border: 1px solid grey;
  border-radius: 300px;
}
.productName:hover {
  color: white;
  background-color: grey;
}
.searchBox {
  margin: 10%;
  width: 120%;
  height: 30%;
}
.cartIcon {
  margin: 0%;
  position: relative;
  top: 40%;
  font-size: 2vw;
  color: white;
  right: 0%;
}
.cartdata {
  color: white;
}
.Cart {
  position: relative;
  height: 3vw;
  right: 5%;
}
